
.portfolio {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-direction: column;
	    flex-direction: column;
}

.portfolio a {
	color: #dfff00;
}

.portfolio a:hover {
	color: #dfff00;
	-webkit-text-decoration-color: #dfff00;
	        text-decoration-color: #dfff00;
}

.header {
	font-size: 30px;
	font-weight: bold;
	font-family: "Roboto", sans-serif;
}

.toolazy {
	width: 50%;
	margin: 10px auto;
	height: 40px;
	line-height: 40px;
	border-top: 1px solid white;
	border-bottom: 1px solid white;
}

.body {
	width: 70%;
	margin: 10px auto;
	font-family: "Lato", sans-serif;
	font-size: 18px;
	line-height: 25px;
}

.para {
	text-align: left;
}

.top {
	margin-right: 10px;
	padding-bottom: 20px;
}

.top > a:hover {
	text-decoration: none;
}

@media only screen and (max-width: 800px){
	.body {
		width: 80%;
	}
	.toolazy {
		width: 70%;
	}
}
@media only screen and (max-width: 500px){
	.body {
		width: 90%;
	}
	.toolazy {
		width: 90%;
	}
}



