.intro-con {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-direction: column;
	    flex-direction: column;
}

.qotd {
	position: absolute;
	width: 100%;
	margin: 0px auto;
	top: 5px;
	text-align: center;
	font-family: "Open Sans", sans-serif;
	opacity: 0.8;
	-webkit-user-select: all;  
	-moz-user-select: all;     
	-ms-user-select: all;      
	user-select: all; 
}
.qotd:hover {
	opacity: 1;
}

.author {
	opacity: 0;
	-webkit-transform: translate(0, -10px);
	    -ms-transform: translate(0, -10px);
	        transform: translate(0, -10px);
}
.title {
	font-family: "Abel", sans-serif;
	font-size: 50px;
	line-height: 40px;
	margin-bottom: 20px;
	text-align: center;
}

.moto {
	font-family: "Open Sans", sans-serif;
	font-size: 18px;
	line-height: 20px;
}

.typing {
	display: inline;
	margin-left: 5px;
}

.links {
	text-align: center;
	margin-top: 15px;
}

.link {
	display: block;
	font-family: "Open Sans", sans-serif;
	margin-top: 10px;
	margin-bottom: 10px;
	text-transform: uppercase;
	font-size: 19px;
	tranition: all 100ms ease-in;
}

.link:hover {
	color: white;
	text-decoration: underline;
	-webkit-filter: brightness(2);
	        filter: brightness(2);
}

.footer {
	position: absolute;
	bottom: 10px;
	right: 10px;;
}

.viewers {
	position: absolute;
	bottom: 10px;
	left: 10px;;
}

* {
	padding-top: 0;
}

@media screen and (max-width: 767px) {
     /* start of medium tablet styles */
     .qotd {
     	width: 80%;
     	font-size: 14px;
     	-webkit-user-select: none;  
		-moz-user-select: none;     
		-ms-user-select: none;      
		user-select: none; 
     }

}

@media screen and (max-width: 479px) {
     /* start of phone styles */
	.qotd {
		width: 90%;
		font-size: 12px;
		-webkit-user-select: none;  
		-moz-user-select: none;     
		-ms-user-select: none;      
		user-select: none; 
	}

}



